import React from 'react';
import {getItemById} from '../../utils/array-utils';
import {connect} from 'react-redux';
import ItemsList from '../components/main/ItemsList';
import {getLocalizedLabel} from '../../utils/localization-util';
import BookItem from '../components/main/BookItem';

const mapStateToProps = function(state) {
    return {populars: state.popularItems,
        selectedCatId : state.menu.selectedCatId,
        menuItems : state.menu.menuItems}
}

const mapDispatchToProps = function(dispatch){
    return {

    }
}

class PopularItemsContainer extends React.Component{

    render(){
        return <section className="books-list-container">
            <h1>{this.buildLabel(this.getMenuItem())}</h1>
            <ItemsList itemRenderer={BookItem}
                       dataProvider={this.props.populars}/>
        </section>
    }

    getMenuItem(){
        return getItemById(this.props.menuItems, this.props.selectedCatId) || {};
    }

    buildLabel(menuItem){
        return getLocalizedLabel(menuItem.localKey, menuItem.name);
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(PopularItemsContainer);


