import React from 'react';
import {connect} from 'react-redux';
import ItemsList from '../components/main/ItemsList';
import {getItemById} from '../../utils/array-utils';
import BookItem from '../components/main/BookItem';

const mapStateToProps = function(state) {
    return {categories: state.categories.categoriesList.categories,
        selectedSubCatId : state.menu.selectedSubCatId,
        selectedCatId : state.menu.selectedCatId}
}

const mapDispatchToProps = function(dispatch){
    return {

    }
}

class BooksListContainer extends React.Component{

    render(){
        return <section className="books-list-container">
            <h1>{this.getCurrentCategory().name}</h1>
            <ItemsList
                itemRenderer={BookItem}
                dataProvider={this.getBooksCollection()}/>
        </section>
    }

    getCurrentCategory(){
        let category = getItemById(this.props.categories, this.props.selectedCatId);
        let subCategory = null;
        if (category != null && category.subCategories != null)
            subCategory = getItemById(category.subCategories, this.props.selectedSubCatId);
        return subCategory || category || {};
    }

    getBooksCollection(){
        let category = this.getCurrentCategory();
        return category != null ? category.items : null;
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BooksListContainer);


