require('array.prototype.find').shim();

import React from 'react';
import {render} from 'react-dom'
import { Provider } from 'react-redux'
import App from './views/App';
import BooksListContainer from './views/containers/BooksListContainer';
import PopularItemsContainer from './views/containers/PopularItemsContainer';
import ContactsView from './views/components/main/ContactsView'
import {store} from './stores/app-store';

import {Router, Route, IndexRoute, browserHistory} from 'react-router';

import {addLocaleData} from 'react-intl';
import ConnectedItnlProvider from './providers/ConnectedItnlProvider';
import en from 'react-intl/locale-data/en';
import ru from 'react-intl/locale-data/ru';

import './../resources/styles/index.less';
import './../resources/styles/common/fonts.less';
import 'normalize.css';

addLocaleData([...en, ...ru]);

render(
    <Provider store={store}>
        <ConnectedItnlProvider>
            <Router history={browserHistory}>
                    <Route path="/" component={App}>
                        <Route path="cat/popular" component={PopularItemsContainer}/>
                        <Route path="cat/contacts" component={ContactsView} />
                        <Route path="cat/:id" component={BooksListContainer}/>
                        <Route path="cat/:id/subCat/:id" component={BooksListContainer}/>
                        <IndexRoute component={PopularItemsContainer}/>
                    </Route>
            </Router>
        </ConnectedItnlProvider>
    </Provider>,
    document.getElementById('root')
)






